import React from 'react';
import Grit from './Grit';

import './styles/NotFound.css';

const NotFound = () => (
  <div id="not-found">
    <div id="text-container">
      <h1>404</h1>
      <h3>Not Found!</h3>
    </div>
    <Grit/>
  </div>
)

export default NotFound;



